import React, {Component} from 'react'; // Component -    react


class Header extends Component {
    render() {
        const {name,surname,variant,group} = this.props;
        return (
            <div className="header">
                <em>
                    <p>{surname + " " + name}</p>
                    <p> {variant}</p>
                    <p> {group}</p>
                </em>
            </div>
        )
    }

}

export default Header;




