import React, {Component} from 'react';
import FormErrors from "./FormErrors";
import {login, registration, setAnswer, setLogin, setPassword} from "../actions/userActions"; // Component -    react
import {connect} from 'react-redux';
import {withRouter} from "react-router"; // Component -    react


class AuthenticationForm extends Component {

    constructor (props) {
        super(props);
        this.state = {
            formErrors: {login: '', password: '', all: ''},
            loginValid: false,
            passwordValid: false,
        };
        this.handleUserLoginInput = this.handleUserLoginInput.bind(this);
        this.handleUserPasswordInput = this.handleUserPasswordInput.bind(this);
        this.handleUserLoginInput = this.handleUserLoginInput.bind(this);
        this.userIsReady = this.userIsReady.bind(this);
    }

    handleUserLoginInput = (e) => {
        let value = e.target.value;
        this.props.setLogin(value);
        this.validateField('login', value);
    };

    handleUserPasswordInput = (e) => {
        let value = e.target.value;
        this.props.setPassword(value);
        this.validateField('password', value);
    };

    userIsReady = (e, option) => {
        let errors = this.state.formErrors;
        if (!this.state.formValid) errors.all = '  ?      ';
        else {
            this.prepareUser(this.props.user.login, this.props.user.password, e, option)
        }
    };

    prepareUser(login, password, event, option) {
        let params = {
            login: login,
            password: password
        };
        if (option === "login") {
            this.props.login(params, event);
        }
        if (option === "registration") {
            this.props.registration(params, event);
        }
    }

    validateField(fieldName, value) {

        this.props.setAnswer('');
        let fieldValidationErrors = this.state.formErrors;
        let loginValid = this.state.loginValid;
        let passwordValid = this.state.passwordValid;

        switch(fieldName) {
            case 'login':
                loginValid = (value.indexOf(' ') < 0);
                fieldValidationErrors.login = loginValid ? '' : '   ';
                if (!loginValid) break;
                loginValid = value.match('^[a-zA-Z0-9]+$');
                fieldValidationErrors.login = loginValid ? '' : ' ';
                if (!loginValid) break;
                loginValid = value.length >= 6;
                fieldValidationErrors.login = loginValid ? '' : ' 6 ?  ???'

                break;
            case 'password':
                passwordValid = (value.indexOf(' ') < 0);
                fieldValidationErrors.password = passwordValid ? '' : '   ';
                if (!passwordValid) break;
                passwordValid = value.match('^[a-zA-Z0-9]+$');
                fieldValidationErrors.password = passwordValid ? '' : ' ';
                if (!passwordValid) break;
                passwordValid = value.length >= 6;
                fieldValidationErrors.password = passwordValid ? '' : ' 6 ?  ???'
                break;
            default:
                break;
        }

        this.setState({formErrors: fieldValidationErrors,
             loginValid: loginValid,
            passwordValid: passwordValid
        }, this.validateForm);
    }

    validateForm() {
        this.setState({formValid: this.state.loginValid &&
                this.state.passwordValid});
    }

    errorClass(error) {
        return(error.length == 0 ? '' : 'input-error' )
    }


    render() {
        const {user} = this.props;
        return (
            <form className="form">
                <div className="inputArea" >
                    <input value={user.login} className={this.errorClass(this.state.formErrors.login)} type="text" placeholder=" "  onChange={this.handleUserLoginInput}/>
                </div>
                <div className="inputArea">
                    <input value={user.password} className={this.errorClass(this.state.formErrors.password)}  type="password" placeholder=" "  onChange={this.handleUserPasswordInput}/>
                </div>
                <div className="formErrorsAuth">
                    <FormErrors formErrors={this.state.formErrors} answer={user.userAnswer}/>
                </div>
                <div className="buttons">
                    <div className="buttonArea">
                        <button className="checkButtonInside" onClick={(event) => this.userIsReady(event, "registration")} disabled={!this.state.formValid}></button>
                    </div>
                    <div className="buttonArea">
                        <button className="checkButtonInside" onClick={(event) => this.userIsReady(event, "login")} disabled={!this.state.formValid}></button>
                    </div>
                </div>

            </form>
        )
    }
}

const mapStateToProps = store =>{
    return{
        user: store.user,
    }
};

const mapDispatchToProps = dispatch => {
    return {
        setAnswer: answer => dispatch(setAnswer(answer)),
        setLogin: login => dispatch(setLogin(login)),
        setPassword: password => dispatch(setPassword(password)),
        registration: (user, event) => dispatch(registration(user, event)),
        login: (user, event) => dispatch(login(user, event))
    }
};

export default (withRouter(connect(mapStateToProps,mapDispatchToProps) (AuthenticationForm)));




